import React, { useState } from 'react'; //  useState-
import { CiSearch } from "react-icons/ci"; // 
import axios from 'axios'; //   
import { Link } from 'react-router-dom'; //   
import SearchDetail from '../pages/SearchDetail'; // SearchDetail  

const Search = () => {
  const imgPath = 'http://image.tmdb.org/t/p/original'; // tmdb 
  const [searchWord, setSearchWord] = useState(''); //  
  const [movies, setMovies] = useState([]); //     
  const [mode, setMode] = useState('list'); //    (list  detail)
  const [selectedMovieID, setSelectedMovieID] = useState(null); //    ID 
  const APIKEY = process.env.REACT_APP_APIKEY;
  
  //   
  const search = () => {
    // TMDB API   
    axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${APIKEY}&language=ko&page=1&include_adult=false&query=${searchWord}`)
      .then((response) => {
        setMovies(response.data.results); //   movies  
      })
      .catch((error) => {
        console.error('Error fetching movies:', error); //     
      });
  };

  //    
  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      search(); // Enter     
    }
  };

  //   
  const showDetail = (movieID) => {
    setMode('detail'); //   
    setSelectedMovieID(movieID); //   ID 

    //     .
    // setDetailInfo({}); //   

    // TMDB API      
    // axios.get(`https://api.themoviedb.org/3/movie/${movieID}?api_key=546c72b99cf64514c2c03c7ef473011b&language=ko`)
    //   .then((response) => {
    //     setDetailInfo(response.data); //   
    //   })
    //   .catch((error) => {
    //     console.error('Error fetching movie details: ', error); //     
    //   });

    //       
    // axios.get(`https://api.themoviedb.org/3/movie/${movieID}/credits?api_key=546c72b99cf64514c2c03c7ef473011b&language=ko`)
    //   .then((response) => {
    //     setActors(response.data.cast); //    
    //   })
    //   .catch((error) => {
    //     console.error('Error fetching movie details: ', error); //     
    //   });
  };

  return (
    <div className='search'>
      <div className="search-box">
        <input 
          type="text" 
          value={searchWord}
          onChange={(e) => { setSearchWord(e.target.value); }} //     
          onKeyPress={handleKeyPress} //    
          placeholder=' ' 
        />
        <button className="search-btn" onClick={search}><CiSearch /></button> {/*        */}
      </div>
      <div className="search-result" style={{ display: mode === 'list' ? 'block' : 'none' }}>
        <ul className="search-movieList">
          {
            movies.map((movie) => (
              <li key={movie.id}>
                <Link to={`/search/${movie.id}`} onClick={() => { showDetail(movie.id); }}> {/*     showDetail  */}
                  <div className="search-list">
                    <div className="img">
                      <img src={`${imgPath}${movie.poster_path}`} alt={movie.title} />
                    </div>
                    <div className="info">
                      <p className="title">{movie.title}</p>
                      <p className="release">{movie.release_date}</p>
                    </div>
                  </div>
                </Link>
              </li> 
            ))
          }
        </ul>
      </div>
      {mode === 'detail' && <SearchDetail selectedMovieID={selectedMovieID} />} {/*    SearchDetail   */}
    </div>
  );
};

export default Search; // Search  